.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}

header {
  margin-top: 1em;
  position: relative;
  overflow: hidden;
  height: 0;
  padding-bottom: 31.5%;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  -webkit-transition: margin 200ms, padding-bottom 200ms;
  -o-transition: margin 200ms, padding-bottom 200ms;
  transition: margin 200ms, padding-bottom 200ms;
}
header.collapsed {
  margin-top: 0.5em;
  padding-bottom: 12%;
}
header a {
  position: absolute;
  width: 100%;
  bottom: 0;
}

.content {
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-x: none;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  margin: 0.5em;
  padding: 0 0.75em 45px 0.75em;
  background: rgba( 255, 255, 255, 0.5 );
  border-radius: 2px;
}

h2, h3 {
  margin: 0.75em 0;
}

.content.loading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.color-scheme-buttons {
  position: fixed;
  height: 0;
  bottom: 0;
  left: 0;
  width: 100%;
}
.color-scheme-buttons button {
  position: absolute;
  bottom: 0;
}
.color-scheme-buttons button:first-child {
  left: 0;
}
.color-scheme-buttons button:last-child {
  right: 0;
}



