.logo path {
  fill: #323232;
  stroke: white;
  stroke-width: 0.1px;
  -webkit-transition: fill 200ms, stroke 200ms, stroke-width 200ms;
  -o-transition: fill 200ms, stroke 200ms, stroke-width 200ms;
  transition: fill 200ms, stroke 200ms, stroke-width 200ms;
}
.collapsed .logo path {
  fill: white;
  stroke-width: 0.1px;
  stroke: white;
  -webkit-transition-delay: 100ms;
       -o-transition-delay: 100ms;
          transition-delay: 100ms;
}
.logo path.white {
  fill: white;
  stroke: #323232;
}



